@import 'variables', 'nav', 'Home', 'fancy-button', 'Kontakt';
html {
  scroll-behavior: smooth;
  scolor: #565655; }

* {
  margin: 0;
  padding: 0;
  text-decoration: none; }

body {
  font-family: 'Lato', sans-serif;
  font-family: 'Raleway', sans-serif; }

.background-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 80vh;
  overflow: hidden;
  z-index: -1;
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    filter: brightness(0.5);
    width: 100%; }
  .text-on-image {
    sbackground-color: rgba(0, 0, 0, .7);
    color: white;
    position: absolute;
    top: 85%;
    left: 50%;
    transform: translate(-50%, -50%);
    // font-family: 'Caveat', cursive
    font-family: 'Raleway', sans-serif;
    font-weight: 200;
    font-size: 30px;
    text-align: center;
    width: 80%; } }
.content {
  min-height: 200px;
  margin-top: 80vh; }
.footer {
  background-color: black;
  color: pink;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  margin-top: 50px; }

@media ( max-width: 600px ) {
  .background-img {
    .text-on-image {
      font-size: 25px; }
    img {
      min-height: 0px; } } }
