.kontakt-container {
  sheight: 500px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  sbackground-color: pink;
  .arrow {
    width: 100%;
    text-align: center; }
  .flexItem {
    flex-basis: 50%; }

  .contact-info {
    sbackground-color: pink;
    margin-top: 50px;
    max-width: 250px;
    min-width: 250px;
    .icon {
      color: royalblue;
      font-size: 24px; }
    p, h3 {
      margin: 20px;
      font-size: 24px;
      border-bottom: 1px dotted black; }
    h3 {
      border-bottom: none;
      margin: 10px; } }

  .contact-form {
    display: flex;
    flex-direction: column;
    align-content: center;
    min-width: 300px;
    max-width: 450px;
    sbackground-color: blue;
    margin-top: 50px;
    h3 {
      margin: 10px;
      font-size: 24px; }

    .row {
      sbackground-color: green;
      text-align: center;
      display: flex;
      margin: 5px;
      input, textarea {
        width: 80%;
        resize: none;
        margin: 5px;
        padding: 10px;
        sbackground-color: lightgrey;
        border-width: 1px;
        border-style: solid;
        font-family: Arial;
        font-size: 16px;
        border-radius: 5px;
        letter-spacing: 1px; }
      .isOK {
        width: 25px;
        height: 25px;
        float: right;
        sbackground-color: pink;
        text-align: center; }
      .error {
        color: #f23333;
        text-align: left;
        margin-left: 5px; } }
    input.button {
      width: 50%;
      background-color: royalblue;
      color: white; }
    input.button:hover {
      background-color: blue;
      transition: 0.4s linear; } }

  .photo-container {
    smargin: 20px;
    margin-top: 50px;
    sbackground-color: pink;
    min-width: 300px;
    smax-width: 450px;
    .photo {
      width: 270px;
      height: 270px;
      sbackground-color: pink;
      position: relative;
      border-radius: 50%;
      overflow: hidden;
      margin: 0 auto;
      img {
        swidth: 100%;
        sheight: 100%;
        position: absolute;
        top: 55%;
        left: 50%;
        transform: translate(-50%, -50%) scale(0.2); } }
    .desc {
      text-align: center;
      margin-top: 20px;
      h3 {
        color: royalblue;
        letter-spacing: 1px; } } }

  .social-media {
    margin-top: 50px;
    sbackground-color: green;
    div {
      border-radius: 10px;
      margin: 10px;
      margin-left: auto;
      margin-right: auto; }

    .fb {
      width: 270px;
      background-color: #3b5998;
      height: 50px;
      overflow: hidden;
      position: relative;
      &:hover {
        filter: brightness(0.9);
        cursor: pointer; }
      img {
        position: absolute;
        top: -45px;
        left: 0;
        transform: scale(0.7); } }
    .hb {
      width: 270px;
      background-color: $colorAlmostWhite;
      border: 1px solid lightgrey;
      &:hover {
        filter: brightness(0.9);
        cursor: pointer; } }
    img {
      width: 100%; } } }

@media ( max-width: 600px ) {
  .kontakt-container {
    flex-direction: column;
    align-items: center;
    sheight: 800px;
    .contact-info {
      width: 300px;
      h3 {
        font-size: 24px; }
      p {
        font-size: 20px; } }

    .contact-form {}
    .photo-container {
      margin: 40px 0px 40px 0px;
      .photo {
        width: 270px;
        height: 270px; } } } }
